.hero {
  height: 100vh;
  background-image: url('../img/hero_2x.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 116px;
    height: 100%;
  }

  &__subtitle {
    text-align: center;
    font-size: 64px;
    font-weight: 400;
    text-transform: uppercase;
    color: #645539;
  }
}
