.stock {
  background-color: var(--gray-color);

  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    padding-top: 90px;
    padding-bottom: 90px;
  }

  &__title {
    margin: 0;
    text-align: center;
    // align-self: baseline;
    writing-mode: vertical-rl;
    font-family: Kudryashev Headline;
    font-size: 80px;
    font-weight: 400;
    line-height: 77px;
    color: var(--dark-color);
    transform: rotate(180deg);
  }

  &__list {
    display: flex;
    grid-column: 2/10 span;
  }

  &__item:not(:last-child) {
    margin-right: 30px;
  }

  &-card {
    display: flex;
    flex-direction: column;
    height: 100%;
    background-color: var(--shamp-color);
    border-radius: 0 0 10px 10px;

    &__content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 100%;
      padding: 10px;
    }

    &__title {
      margin: 0;
      margin-bottom: 15px;
      font-size: 24px;
      font-weight: 500;
      line-height: 32px;
      color: var(--dark-color);
      text-transform: uppercase;
    }

    &__price {
      align-self: center;
      font-size: 32px;
      font-weight: 700;
      line-height: 24px;
      text-transform: uppercase;
      color: #b86f4c;
    }
  }
}
