.header {
position: absolute;
top: 0;
left: 0;
right: 0;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-fluid {
      background-color: var(--matt-color);
      backdrop-filter: blur(10px);

      &--top {
        background-color: var(--dark-color);
      }
    }

    &--top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-top: 7px;
      padding-bottom: 7px;
    }

  }

  &__logo {
    padding-top: 3px;
    background-position: 5px;
  }

  &__nav {

    &-list {
      display: flex;
    }

    &-item:not(:last-child) {
      margin-right: 39px;
    }

    &-link {
      font-size: 16px;
      font-weight: 400;
      color: var(--dark-color);
    }
  }

  &__location {
    margin: 0;
    padding-left: 36px;
    width: 25%;
    font-size: 16px;
    font-weight: 400;
    line-height: normal;
    color: var(--shamp-color);
    background-image: url('../img/location.svg');
    background-repeat: no-repeat;
    background-position: left center;
  }
}

.btn {
  padding: 10px;
  text-align: center;
  font-size: 16px;
  font-weight: 400;
  border-radius: 10px;
  color: var(--dark-color);
  background-image: linear-gradient(244deg, #C78853 33.49%, #D5BDA1 49.48%, #D9D9D9 53.82%, #EFE5D6 56.56%, #D2B589 77.34%);
}
