// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "TT Norms Pro", sans-serif;
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --dark-color: #000000;
  --shamp-color: #D2B589;
  --gray-color: #E8E5E0;
  --matt-color: #ffffff64;
  --gradient: linear-gradient(244deg, #C78853 33.49%, #D5BDA1 49.48%, #D9D9D9 53.82%, #EFE5D6 56.56%, #D2B589 77.34%);
}

